.cause-card {
  height: 220px;
  width: 100%;
  background: white;
  box-shadow: 0 7px 20px 3px rgba(162, 180, 198, 0.3), inset 2px 2px 4px 0 rgba(255, 255, 255, 0.5);
  border: solid 1px #f7f9fc;
  padding: 20px;
  border-radius: 20px;
  margin-bottom: 42px;
  display: inline-flex;

  &__name {
    font-family: 'Lato-Black';
    font-size: 16px;
    margin: 0px;
    color: #212121;
    margin-bottom: 6px;
  }
  &__org {
    font-family: 'Lato-Regular';
    font-size: 16px;
    margin: 0px;
    color: #212121;
    margin-bottom: 6px;
  }
  &__location {
    font-family: 'Lato-Light';
    font-size: 16px;
    margin: 0px;
    color: #212121;
    margin-bottom: 6px;
  }

  &__left {
    height: 100%;
    width: 90px;
    margin-right: 15px;
    position: relative;
  }

  &__logo {
    height: 100%;
    width: 90px;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &__sector-icon {
    height: 55px;
    width: 55px;
    border-radius: 50%;
    background: #e5e9fb;
    position: absolute;
    bottom: 0;
  }

  &__right {
    height: 100%;
    width: 100%;
    // background: blue;
    // margin-right: 15px;
  }
}